.dialog-popup {
	display: flex;
	justify-content: center;
	align-items: flex-start;
	overflow: hidden;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: -1;
	width: 100%;
	height: 100%;
	padding: 20px;
	box-sizing: border-box;
	background-color: rgba(0, 0, 0, 0.4);
	transition: visibility 0.15s 0.3s, z-index 0s 0.3s, opacity 0.2s;
	opacity: 0;

	&.active {
		z-index: 100;
		transition-delay: 0s;
		opacity: 1;
		visibility: visible;
	}

	.popup-wrap {
		flex: 1 1 100%;
		align-self: center;
		overflow: hidden;
		display: flex;
		flex-direction: column;
		max-width: 400px;
		max-height: 100%;
		border-radius: 8px;
		background-color: var(--color-base-100);
		box-sizing: border-box;

		.popup-header {
			display: flex;
			justify-content: space-between;
			padding: 8px 8px 0;
	
			.btn-popup-close {
				flex-shrink: 0;
				overflow: hidden;
				position:relative;
				z-index: 20;
				width: 32px;
				height: 32px;
				margin-left: auto;
				text-indent: -999px;

				&::before {
					content: '';
					display: block;
					width: 32px;
					height: 32px;
					mask: icon(close) no-repeat 50%/cover;
					background-color: var(--color-base-050);
				}
			}
		}
	
		.popup-body {
			padding: 0 0 32px;
			@include overflow-scroll(default, dark, medium, y);
	
			.popup-contents {
				padding: 0 40px;
	
				>.para {
					font-size: 18px;
					line-height: 28px;
					text-align: center;

					.wbr {
						display: block;
						word-break: keep-all;
					}
				}

				.align-ct {
					text-align: center;
				}
			}
		}
	
		.popup-footer {
			border-top: 1px solid var(--color-base-080);
	
			.button-area {
				display: flex;
				column-gap: 0;
	
				[class*=btn-] {
					display: inline-flex;
					justify-content: center;
					align-items: center;
					flex: 1 1 100%;
					height: 52px;
					background-color: var(--color-base-100);
					font-weight: 500;
					line-height: 24px;
					color: var(--color-base-010);
	
					&:last-child {
						background-color: var(--color-base-010);
						color: var(--color-base-100);
					}
				}
			}
		}
	}

	@include respond-below(tablet) {
		.popup-wrap {
			min-width: 100%;

			.popup-body {
				.popup-contents {
					padding: 0 24px;

					>.para {
						max-width: 280px;
						margin: 0 auto;
						font-size: 16px;
						line-height: 24px;
						word-break: keep-all;
					}
				}
			}
		}
	}
}

.content-popup {
	display: flex;
	justify-content: center;
	align-items: flex-start;
	overflow: hidden;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: -1;
	width: 100%;
	height: 100%;
	padding: 20px;
	box-sizing: border-box;
	background-color: rgba(0, 0, 0, 0.4);
