			max-width: 720px;
		}
	}

	&.active {
		z-index: 100;
		transition-delay: 0s;
		opacity: 1;
		visibility: visible;
	}

	.popup-wrap {
		flex: 1 1 100%;
		align-self: center;
		overflow: hidden;
		display: flex;
		flex-direction: column;
		max-height: 100%;
		border-radius: 8px;
		box-sizing: border-box;
		background-color: var(--color-base-100);
		box-shadow: 3px 4px 20px 0px rgba(0, 0, 0, 0.25);

		.popup-header {
			display: flex;
			position: relative;
			padding: 18px 16px;
			box-shadow: inset 0 -1px 0 var(--color-base-060);
	
			.popup-subject {
				display: flex;
				align-items: center;
				column-gap: 10px;
				font-size: 18px;
				font-weight: 700;
				line-height: 28px;
				color: var(--color-base-010);
			}
	
			.btn-popup-close {
				flex-shrink: 0;
				overflow: hidden;
				position: absolute;
				top: 18px;
				right: 16px;
				left: auto;
				z-index: 20;
				width: 30px;
				height: 30px;
				text-indent: -999px;

				&::before {
					content: '';
					display: block;
					width: 30px;
					height: 30px;
					mask: icon(close) no-repeat 50%/cover;
					background-color: var(--color-base-010);
				}
			}
		}
	
		.popup-body {
			padding: 16px;
			@include overflow-scroll(default, dark, medium, y);
		}
	
		.popup-footer {
			padding: 16px;
			box-sizing: border-box;
			box-shadow: inset 0 1px 0 var(--color-base-060);
			
			.button-area {
				display: flex;
				column-gap: 8px;
				justify-content: center;
	
				[class*=btn-] {
					display: inline-flex;
					justify-content: center;
					align-items: center;
					flex: 1 1 100%;
					// height: 52px;
					// background-color: var(--color-base-100);
					// font-weight: 500;
					// line-height: 24px;
					// color: var(--color-base-010);

					&:first-child {
						max-width: 120px;
						// background-color: var(--color-primary-010);
						// color: var(--color-base-100);
					}
				}
			}
		}
	}

	// &.small {
	// 	.popup-wrap {
	// 		max-width: 480px;
	// 	}
	// }

	@include respond-below(tablet) {
		.popup-wrap {
			max-width: 100% !important;
			padding-bottom: 0px;

			.popup-header {
				padding:24px 52px 0;

				.popup-subject {
					font-size: 20px;
					line-height: 30px;

					br {
						display: none;
					}
				}

				.btn-close {
					top: 25px;
					right: 16px;
					width: 28px;
					height: 28px;
				}
			}

			.popup-body {
				.modal-contents {
					padding: 0 24px;
				}
			}

			.popup-footer {		
				.button-area {
					column-gap: 0;
	
					.btn {
						max-width: 100%;
					}
				}
			}
		}
	}
}

.toast-popup {
	display: flex;
	justify-content: center;
	align-items: flex-start;
	overflow: hidden;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: -1;
	width: 100%;
	height: 100%;
	padding: 40px 20px;
	box-sizing: border-box;
	transition: visibility 0.15s 0.3s, z-index 0s 0.3s, opacity 0.2s;
	opacity: 0;

	&.active {
		z-index: 100;
		transition-delay: 0s;
		opacity: 1;
		visibility: visible;

		.popup-wrap {
			transform: translateY(0);
		}
	}

	.popup-wrap {
		flex: 1 1 100%;
		align-self: flex-end;
		overflow: hidden;
		display: flex;
		flex-direction: column;
		max-width: 320px;
		max-height: 100%;
		box-sizing: border-box;
		border-radius: 4px;
		background-color: rgba(0, 0, 0, 0.90);
		transform: translateY(100%);
		transition: transform 0.3s ease-in-out;

		.popup-body {
			.popup-contents {
				padding: 12px 24px;

				.para {
					font-size: 16px;
					font-weight: 500;
					line-height: 24px;
					color: var(--color-base-100);
					text-align: center;
				}
			}
		}
	}

	// &.small {
	// 	.popup-wrap {
	// 		max-width: 320px;
	// 	}
	// }

	@include respond-below(tablet) {
		padding: 32px 20px;

		.popup-wrap {
			max-width: 100% !important;
		}
	}
}

.fullscreen-popup {
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	background-color: rgba(0, 0, 0, 0.90);
	display: flex;
	justify-content: center;
	align-items: center;
	opacity: 0;
	z-index: 100;
	visibility: hidden;
	transition: opacity 0.3s ease;
	

	&.active {
		opacity: 1;
		visibility: visible;
	}

	.popup-content {
		padding: 20px;
		border-radius: 10px;
	}

	.popup-header {
		.popup-subject {
			position: absolute;
			top: 30px;
			left: 24px;
			width: 155px;
			height: 20px;
			mask-image: url(../../../assets/admin/images/ucomp-logo.svg);
			background-color: var(--color-base-100);

			.popup-name {
				@include hidden;
			}
		}

		.button-display {
			display: flex;
			align-items: center;
			column-gap: 20px;
			position: absolute;
			top: 29px;
			right: 36px;
			left: auto;
			z-index: 20;
			flex-shrink: 0;
				
			.btn-popup-search,
			.btn-popup-mypage,
			.btn-popup-close {
				overflow: hidden;
				width: 32px;
				height: 32px;
				text-indent: -999px;

				&::before {
					content: '';
					display: block;
					width: 32px;
					height: 32px;
					background-color: var(--color-base-100);
					mask-position: center;
				}
			}

			.btn-popup-search {
				&::before {
					mask: icon(usersearch) no-repeat 50%/cover;
				}
			}

			.btn-popup-mypage {
				&::before {
					background-color: var(--color-base-040);
					mask: icon(my-page) no-repeat 50%/cover;
				}
			}

			.btn-popup-close {
				&::before {
					mask: icon(close) no-repeat 50%/cover;
				}
			}
		}

	}

	.menu-display {
		width: 1280px;
		color: var(--color-base-100);

		.menu-list {
			display: flex;
			align-items: flex-start;
			justify-content: space-between;

			.menu-item {
				display: flex;
				flex-direction: column;
				row-gap: 24px;

				.menu-head {
					line-height: 40px;
					font-size: 28px;
					font-weight: 700;
				}

				.menu-body {
					.sub-list {
						display: flex;
						flex-direction: column;
						row-gap: 16px;

						.sub-item {
							.sub-name {
								display: block;

								&:hover {
									text-decoration: underline;
								}
							}
						}
					}
				}
			}
		}
	}
}


.side-popup {
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
    position: fixed;
    top: 0;
    right: -200%; 
    width: 660px; 
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    transition: right 0.5s ease; 
    z-index: 1000; 

    &.active {
        right: 0; 
    }

    .popup-wrap {
        flex: 1;
        background-color: var(--color-base-100);
        padding: 20px;
		box-shadow: 3px 4px 20px 0px rgba(0, 0, 0, 0.25);
        height: 100%;
        overflow-y: auto; 
		box-sizing: border-box;
    }

    .popup-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 24px;

        .popup-subject {
            font-size: 24px;
			line-height: 36px;
            font-weight: 700;
            color: var(--color-base-010);
        }

		.btn-popup-close {
			flex-shrink: 0;
			overflow: hidden;
			z-index: 20;
			width: 36px;
			height: 36px;
			text-indent: -999px;

			&::before {
				content: '';
				display: block;
				width: 36px;
				height: 36px;
				mask: icon(close) no-repeat 50%/cover;
				background-color: var(--color-base-050);
			}
		}
    }

    .popup-body {
		max-height: 730px;
        padding: 0 0 20px;
		@include overflow-scroll(default, dark, medium, y);

		.subsection {
			display: flex;
			flex-direction: column;
			row-gap: 12px;

			+.subsection {
				margin-top: 40px;
			}

			.subsection-head {
				display: flex;
				align-items: center;
				justify-content: space-between;

				.subsection-subject {
					display: flex;
					align-items: center;
					column-gap: 8px;

					.subsection-name {
						line-height: 28px;
						font-size: 18px;
						font-weight: 700;
						color: var(--color-base-010);
					}

					.mark-list {
						display: flex;
						column-gap: 8px;

						.mark-item {
							padding: 2px 8px;
							border-radius: 90px;
							box-sizing: border-box;
							line-height: 20px;
							font-size: 14px;
							font-weight: 600;

							&.type-blue {
								color: #0092FB;
								background-color: rgba(0, 146, 251, 0.08);
							}

							&.type-green {
								color: #00C48A;
								background-color : rgba(0, 196, 138, 0.08);
							}

							&.type-gray {
								color: #9FACBA;
								background-color: rgba(159, 172, 186, 0.08);
							}

							&.type-red {
								color: #FF314E;
								background-color: rgba(255, 49, 78, 0.08);
							}
						}
					}
				}
			}
		}
    }

	.popup-footer {
		.button-area {
			position: absolute;
			right: 24px;
			bottom: 24px;
			left: 24px;
			display: flex;
			align-items: center;
			justify-content: flex-end;
			column-gap: 8px;

			[class*=btn-] {
				display: inline-flex;
				justify-content: center;
				align-items: center;
				flex: 1 1 100%;
				height: 40px;
				font-weight: 600;
				line-height: 20px;
				font-size: 14px;
				border-radius: 4px;
				min-width: 98px;

				&.save {
					min-width: 400px;
					color: var(--color-base-100);
					background-color: var(--color-primary-010);
				}

				&.save.bk {
					background-color: var(--color-base-020);
				}

				&.delete {
					color: var(--color-base-010);
					background-color: var(--color-base-070);
				}

				&.cancel {
					// min-width: 40px;
					color: var(--color-base-010);
					border: 1px solid var(--color-base-060);
					background-color: var(--color-base-100);
					// background-image: icon(delete);
