				padding: 0 12px;
				border: none;
				height: 56px;
				margin: 1px;
			}
	
		}
	
		.member-display {
			padding: 8px;
			box-sizing: border-box;
			border-top: 1px solid var(--color-base-060);
	
			.data-list {
				.data-item {
					position: relative;
					padding: 8px 12px;
					box-sizing: border-box;
					overflow: hidden;
	
					&::after {
						content: '';
						display: block;
						position: absolute;
						top: 18px;
						right: 6px;
						width: 20px;
						height: 20px;
						background-color: var(--color-base-050);
						mask-image: icon(chevron-right);
						mask-repeat: no-repeat;
						mask-position: center;
						mask-size: cover;
					}
	
					&:hover {
						border-radius: 4px;
						background-color: var(--color-base-080);
					}
	
					.data-wrap {
						.data-head {
							.data-name {
								line-height: 20px;
								font-size: 14px;
								font-weight: 600;
								color: var(--color-base-010);
								text-decoration: underline;
	
								&::before {
									content: '';
									display: block;
									position: absolute;
									top: 0;
									right: 0;
									bottom: 0;
									left: 0;
								}
							}
						}
						.data-body {
							display: flex;
							align-items: center;
							margin-top: 4px;
							line-height: 16px;
							font-size: 12px;
							color: var(--color-base-030);
	
							.data-email {
	
							}
							.data-phone {
								display: flex;
								align-items: center;
	
								&::before {
									content: '';
									display: block;
									margin: 0 6px;
									width: 3px;
									height: 3px;
									border-radius: 50%;
									background-color: var(--color-base-040);
								}
							}
						}
					}
				}
			}
		}

		.info-board {
			padding: 30px 20px;
			box-sizing: border-box;
			text-align: center;
			border-top: 1px solid var(--color-base-060);

			.board-wrap {
				.board-head {
					.board-subject {
						line-height: 20px;
						font-size: 14px;
						font-weight: 600;
						color: var(--color-base-050);
					}
				}
			}
		}
	}

