.data-table {
	width: 100%;

	table {
		position: relative;
		width: 100%;

		&::before {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 1px;
			background-color: var(--color-base-000);
		}

		thead {
			th {
				padding: 16px;
				border-bottom: 0;
				// border-bottom: 1px solid #dfdfdf;
				background-color: #f7f7f7;
				font-size: 14px;
			}
		}

		tbody {
			th,
			td {
				padding: 12px 20px;
				border-width: 1px;
				border-style: solid;
				border-color: #dedede;
				// border-bottom: 1px solid #ebebeb;
				font-size: 14px;
				text-align: center;

				&:first-child {
					border-left: none;
				}

				&:last-child {
					border-right: none;
				}
			}

			th {
				font-weight: 600;
			}

			.align {
				&-lt {
					text-align: left;
				}

				&-rt {
					text-align: right;
				}

				&-ct {
					text-align: center;
				}
			}
		}
	}
}