.tab-display {
	&.style-a {
		width: 100%;

		.tab-list {
			display: flex;
			list-style: none;
			padding: 0 24px;
			margin: 0;
			position: relative;
			box-sizing: border-box;
			overflow: hidden;
			background-color: var(--color-base-080);
			
			&::before {
				content: '';
				display: block;
				height: 1px;
				left: calc(50% - 50vw);
				width: 100vw;
				position: absolute;
				bottom: 0px;
				background-color: var(--color-base-060);
				// z-index: -1;

			}

			.tab-item {
				&.current {
					.tab-text {
						border: 1px solid var(--color-base-060); 
						border-bottom: 1px solid var(--color-base-100);
						z-index: 10;
						color: var(--color-primary-010);
						background-color: var(--color-base-100);
					}
				}

				.tab-text {
					position: relative;
					display: block;
					padding: 16px 26px;
					text-decoration: none;
					line-height: 24px;
					font-size: 16px;
					color: var(--color-base-050); // 기본 탭 색상
					font-weight: 600;
					box-sizing: border-box;
				}
			}
		}
	}

	&.style-b {
		.tab-list {
			overflow: hidden;
			display: flex;
			gap: 12px;
			padding: 8px;
			border-radius: 16px;
			background-color: var(--color-base-090);

			.tab-item {
				border-radius: 16px;
				background-color: var(--color-base-095);

				&.current {
					background-color: var(--color-primary-040);

					.tab-text {
						font-weight: 500;
						color: var(--color-base-100);
					}
				}

				.tab-text {
					display: block;
					position: relative;
					padding: 8px 12px;
					// box-shadow: inset 0px -1px 0px color(base, 060);
					// background-color: var(--color-base-100);
					font-size: 16px;
					line-height: 24px;
					color: var(--color-base-010);
					text-align: center;
					cursor: pointer;
				}
			}
		}
	}
}

.tab-content {
	visibility: visible;
	opacity: 1;
	transition: opacity 0.3s ease, visibility 0.3s ease;

	&:not(.on) {
		display: none;
		visibility: hidden;
		opacity: 0;
	}
}