  mask-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 '%3E %3Cpath /%3E%3C/svg%3E");
  mask-repeat: no-repeat;
  mask-position: center center;
  mask-size: 20px;
}
.data-finder .data-rbx .board-search-box .form.input .form-elem[type=text]::placeholder {
  color: var(--color-base-060);
}
.data-finder .data-rbx .button-area {
  flex-shrink: 0;
  column-gap: 8px;
}

/********************************************************************************************************************************

 * pagination

********************************************************************************************************************************/
.pagination {
  margin-top: 38px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.pagination .page-btn {
  flex-shrink: 0;
  display: block;
  width: 20px;
  height: 20px;
  box-sizing: border-box;
}
.pagination .page-btn + .page-btn {
  margin-left: 8px;
}
.pagination .page-btn::before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  mask-size: 20px;
  mask-repeat: no-repeat;
  mask-position: center;
  background-color: var(--color-base-050);
}
.pagination .page-btn.page-prev::before {
  mask-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'%3E %3Cpath fill='none' stroke='rgba(0,0,0,1)' stroke-width='1.5' d='m15.1 7-5 5 5 5'/%3E%3C/svg%3E");
}
.pagination .page-btn.page-next::before {
  mask-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'%3E %3Cpath fill='none' stroke='rgba(0,0,0,1)' stroke-width='1.5' d='m9.7 7 5 5-5 5'/%3E%3C/svg%3E");
}
.pagination .page-num {
  display: flex;
  column-gap: 8px;
  margin: 0 8px;
  transition: all 0.3s ease-in-out;
}
.pagination .page-num .page-link {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  position: relative;
  padding: 0 8px;
  min-width: 16px;
  height: 32px;
  font-size: 14px;
  font-weight: 500;
  color: var(--color-base-020);
  transition: all 0.3s;
  letter-spacing: -0.56px;
  border: 1px solid #DEE2E8;
  border-radius: 4px;
}
.pagination .page-num .page-link.current {
  color: var(--color-primary-010);
  border-color: var(--color-primary-010);
}

/********************************************************************************************************************************

 * editor

********************************************************************************************************************************/
.ck.ck-editor__editable_inline > :last-child {
