/********************************************************************************************************************************

 * typography

********************************************************************************************************************************/
// 페이지 단위 타이틀
.heading-l {
	font-size: 56px;
	font-weight: 700;
	line-height: 150%;
}

.heading-m {
	font-size: 48px;
	font-weight: 700;
	line-height: 150%;
}

.heading-s {
	font-size: 32px;
	font-weight: 700;
	line-height: 150%;
}

// 템플릿, 모듈 단위 텍스트
.title-l {
	font-size: 32px;
	font-weight: 700;
	line-height: 150%;
}

.title-m {
	font-size: 28px;
	font-weight: 500;
	line-height: 150%;
}

.title-s {
	font-size: 24px;
	font-weight: 500;
	line-height: 150%;
}

// body 본문 텍스트
.body-l {
	font-size: 24px;
	font-weight: 400;
	line-height: 150%;
}

.body-m {
	font-size: 16px;
	font-weight: 400;
	line-height: 150%;
}

.body-s {
	font-size: 14px;
	font-weight: 400;
	line-height: 150%;
}

@include respond-below(tablet) {
	.heading-l {
		font-size: 40px;
	}
	
	.heading-m {
		font-size: 36px;
	}
	
	.heading-s {
		font-size: 32px;
		font-weight: 700;
		line-height: 150%;
	}
	
	// 템플릿, 모듈 단위 텍스트
	.title-l {
		font-size: 24px;
	}
	
	.title-m {
		font-size: 20px;
	}
	
	.title-s {
		font-size: 18px;
	}
	
	// body 본문 텍스트
	.body-l {
		font-size: 18px;
	}
	
	.body-m {
		font-size: 14px;
	}
	
	.body-s {
		font-size: 12px;
	}
}

/********************************************************************************************************************************

 * data-finder

********************************************************************************************************************************/


//data-filter
.data-filter {
display: flex;
flex-direction: column;
align-items: baseline;
margin-bottom: 16px;

&.type-a {
	&::after {
		content: "";
		display: block;
		width: 100%;
		height: 1px;
		background-color: var(--color-base-090);
		margin-top: 16px;
	}
}


&.type-b {
	padding: 32px;
	border-radius: 20px;
	background-color: var(--color-base-100);

}

.data-form {
	display: flex;
	flex: 1 1 100%;
	width: 100%;
	align-items: flex-end;
	column-gap: 16px;

	.submit-form {
		.form-list {
			display: flex;
			column-gap: 12px;

			.form-item {
				.form-wrap {
					display: flex;
					flex-direction: column;
					row-gap: 4px;		
					
					.form-head {
						.form-name {
							line-height: 20px;
							font-size: 14px;
							color: var(--color-base-030);
						}
					}
				}
			}
		}
	}

	.button-display {
		flex-shrink: 0;

		.button-area {
			column-gap: 6px;
		}
	}
}
}

.data-finder {
	display: flex;
	align-items: flex-end;
	justify-content: space-between;
	margin-bottom: 12px;

	.data-lbx {
		.data-list {
			display: flex;
			align-items: center;

			.data-item {
				line-height: 20px;
				font-size: 14px;
				color: var(--color-base-030);

				+ .data-item {
					display: flex;
					align-items: center;
					padding-left: 10px;

					&::before  {
						content: '';
						display: block;
						margin-right: 10px;
						width: 1px;
						height: 14px;
						background-color: var(--color-base-060);
					}
				}

				b {
					margin-left: 0.25em;
					color: var(--color-base-010);
				}
			}
		}
	}

	.data-rbx {
		display: flex;
		align-items: center;
		column-gap: 8px;

		.board-search-box {
			.form.input {
				.form-elem[type=text] {
					background-color: inherit;
					padding-left: 38px;
					border-color: var(--color-base-080);
				}
			}
			.form.input::before {
				content: '';
				display: block;
				position: absolute;
				top: 8px;
				left: 10px;
				width: 20px;
				height: 20px;
				background-color: var(--color-base-060);
				mask-image: icon(search);
				mask-repeat: no-repeat;
				mask-position: center center;
				mask-size: 20px;
			}
	
			.form.input .form-elem[type=text]::placeholder {
				color: var(--color-base-060);
			}
		}

		.button-area {
			flex-shrink: 0;
			column-gap: 8px;
		}
	}
}

/********************************************************************************************************************************

 * pagination

********************************************************************************************************************************/
.pagination {
	margin-top: 38px;
	display: flex;
	justify-content: center;
	align-items: center;

	.page-btn {
		flex-shrink: 0;
		display: block;
		width: 20px;
		height: 20px;
		box-sizing: border-box;

		+.page-btn {
			margin-left: 8px;
		}

		&::before {
			content: '';
			display: block;
			width: 100%;
			height: 100%;
			mask-size: 20px;
			mask-repeat: no-repeat;
			mask-position: center;
			background-color: var(--color-base-050);
		}

		// &.page-first::before { mask-image: icon(chevron-left-last); }
		&.page-prev::before { mask-image: icon(chevron-left); }
		&.page-next::before { mask-image: icon(chevron-right); }
		// &.page-last::before { mask-image: icon(chevron-right-last); }
	}

	.page-num {
		display: flex;
		column-gap: 8px;
		margin: 0 8px;
		transition: all .3s ease-in-out;

		.page-link  {
			display: inline-flex;
			justify-content: center;
			align-items: center;
			position: relative;
			padding: 0 8px;
			min-width: 16px;
			height: 32px;
			font-size: 14px;
			font-weight: 500;
			color: var(--color-base-020);
			transition: all .3s;
			letter-spacing: -0.56px;
			border: 1px solid #DEE2E8;
			border-radius: 4px;

			&.current {
				color: var(--color-primary-010);
				border-color: var(--color-primary-010);
			}
		}
	}
}



