.btn,
.btn-a {
	// 기본 설정
	--height: 40px;
	--padding: 0 16px;
	--radius: 4px;
	--fontSize: 14px;
	--fontWeight: 500;
	--lineHeight: 20px;
	--iconBg: var(--color-base-010);

	display: inline-flex;
	align-items: center;
	justify-content: center;
	width: auto;
	height: var(--height);
	padding: var(--padding);
	box-sizing: border-box;
	// box-shadow: inset 0px 0px 0 1px var(--borderColor);
	border-radius: var(--radius);
	background-color: var(--bgColor);
	border: 1px solid var(--borderColor);
	font-size: var(--fontSize);
	font-weight: var(--fontWeight);
	line-height: var(--lineHeight);
	color: var(--color);
	text-align: center;

	&:focus {
		outline-offset: -2px;
		outline: 2px solid rgba(36, 107, 235, 0.8);
	}

	// &:not(.disabled):not(:disabled) {
	// 	--borderColor: var(--color-base-020);
	// 	--color: var(--color-base-100);
	// }

	&:not([class*="style-"]):not(.disabled) {
		--borderColor: var(--color-base-060);
		--color: var(--color-base-010);
	  }

	&[disabled],
	&.disabled {
		--iconBg: var(--color-base-010)
	}

	// style
	&.style-a:not(.disabled):not(:disabled) {
		--bgColor: var(--color-base-020);
		--color: var(--color-base-100);
		--iconBg: var(--color-base-100);
	}

	&.style-b:not(.disabled):not(:disabled) {
		--bgColor: var(--color-primary-010);
		--color: var(--color-base-100);
		--iconBg: var(--color-base-100);
	}

	&.style-c:not(.disabled):not(:disabled) {
		--bgColor: var(--color-base-100);
		--color: var(--color-primary-010);
		--iconBg: var(--color-primary-010);
		--borderColor: var(--color-primary-010);
	}

	&.style-d:not(.disabled):not(:disabled) {
		--bgColor: var(--color-secondary-010);
		--color: var(--color-base-100);
		--iconBg: var(--color-base-100);
	}

	&.style-e:not(.disabled):not(:disabled) {
		--bgColor: var(--color-base-070);
		--color: var(--color-base-010);
		--iconBg: var(--color-base-010);
	}

	&.excel:not(.disabled):not(:disabled) {
		--bgColor: var(--color-base-100);
		--color: #358142;
		--iconBg: #358142;
		--borderColor: #358142;
	}
	

	// ico
	&[class*="ico-"] {
		--ico-width: 14px;
		--ico-height: 14px;
		gap: 4px;

		&:not([class*=-rtl]) {
			&::before {
				content: '';
				display: block;
				width: var(--ico-width);
				height: var(--ico-height);
				mask-repeat: no-repeat;
				mask-position: 50%;
				mask-size: cover;
				background-color: var(--iconBg);
			}
		}
		
		&[class*=-rtl] {
			&::after {
				content: '';
				display: block;
				width: var(--ico-width);
				height: var(--ico-height);
				mask-repeat: no-repeat;
				mask-position: 50%;
				mask-size: cover;
				background-color: var(--iconBg);
			}
		}		
	}

	// &[class*=ico-pin] { &::before, &::after { mask-image: icon(g-pin, --color-base-100); } }

	// size
	$btn-style-size: (
		// name size font-size padding
		'small' 32px 14px 10px,
		'medium' 40px 14px 16px,
	);
	
	@each $name, $size, $fontSize, $padding in $btn-style-size {
		&.#{$name} {
			// --min-width: #{$outline};
			--height: #{$size};
			--lineHeight: #{$size};
			--fontSize: #{$fontSize};
			--padding: 0 #{$padding};

			&[class*="ico-"] {
				--padding: 0 calc(#{$padding} - 4px) 0 #{$padding};

				&::before,
				&::after {
					width: #{$fontSize};
					height: #{$fontSize};
				}
			}
		}
	}

	&.btn-text {
		padding: 0;
		background-color: inherit;
		border: 0;
		color: inherit;

		&.underline {
			text-decoration: underline;
		}

		&.link {
			height: auto;
			line-height: 1;

			&:hover {
				text-decoration: underline;
			}
		}

		&[class*="ico-"] {
			--iconBg: var(--color-base-000);
			// 텍스트 컬러랑 같아야함

			gap: 2px;
		}

		&:hover {
			box-shadow: none;
		}
	}
}

:where([class*=btn-]){
	&[class*="disabled"],
	&[disabled] {
		--bgColor: var(--color-base-080);
		--color: var(--color-base-10);

		background-color: var(--bgColor);
		color: var(--color);
		box-shadow: none;

		&:hover {
			box-shadow: none;
		}
	}

	&:focus {
		outline-offset: -2px;
		outline: 2px solid rgba(36, 107, 235, 0.8);
	}

	&.flex {
		flex: 1 1 100%;
	}

	&[class*=ico-settings] { &::before, &::after { mask-image: icon(settings, --color-base-100); } }
	&[class*=ico-download] { &::before, &::after { mask-image: icon(download, --color-base-040); background-color: var(--color-base-040);} }
	&[class*=ico-delete] { &::before, &::after { mask-image: icon(delete, --color-base-040); background-color: var(--color-base-040); } }
	&[class*=ico-arrow-left] { &::before, &::after { mask-image: icon(arrow-left, --color-base-000); background-color: var(--color-base-000); } }
	&[class*=ico-home] { &::before, &::after { mask-image: icon(home, --color-base-100); background-color: var(--color-base-100); } }

}

.button-display {
	display: flex;
	justify-content: center;

	&.align-lt {
		justify-content: flex-start;
	}

	&.align-rt {
		justify-content: flex-end;
	}

	&.align-ct {
		justify-content: center;
	}

	.button-area {
		flex: 1 1 100%;
	}
}

.button-area {
	display: flex;

	&.align-lt {
		justify-content: flex-start;
	}

	&.align-rt {
		justify-content: flex-end;
	}

	&.align-ct {
		justify-content: center;
	}
}
