// 공통 컴퍼넌트
@import 'partials/_base';
@import 'partials/_variables';
// @import 'partials/_mixin';
// @import 'partials/_color';
// @import 'partials/_icons';
@import 'partials/_vendor';

@import 'partials/_button';
@import 'partials/_form';
@import 'partials/_table';
@import 'partials/_tab';
@import 'partials/_popup';
@import 'partials/_etc';

:root {
	@each $variables in $COLOR-PALETTES {
		@each $color-name in map-get($COLOR-PALETTES, nth($variables, 1)) {
			--color-#{nth($variables, 1)}-#{nth($color-name, 1)}: #{nth($color-name, 2)};
		}
	}
}
